.board {
	display: grid;
	grid-template-columns: repeat(var(--grid-size), var(--tile-size));
	grid-template-rows: repeat(var(--grid-size), var(--tile-size));
	gap: 6px;
	justify-content: center;
	align-items: center;
	margin: 20px auto;
}

.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(11, 23, 36, 0.9);
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	font-size: 2.2rem;
	font-weight: bold;
	z-index: 10;
	animation: fadeIn 0.5s ease-in-out;
	flex-direction: column;
	padding: 20px;
}

.overlay.win {
	color: #00ff90;
	text-shadow: 0 0 12px rgba(0, 255, 144, 0.7);
}

.overlay.lose {
	color: #ff4d4d;
	text-shadow: 0 0 12px rgba(255, 77, 77, 0.7);
}

.restart-btn {
	margin-top: 20px;
	padding: 10px 20px;
	background: #1c2833;
	border: 2px solid #00ff90;
	color: #00ff90;
	font-size: 1.2rem;
	cursor: pointer;
	border-radius: 6px;
	transition: 0.3s;
}
.restart-btn:hover {
	background: #243340;
	box-shadow: 0 0 10px rgba(0, 255, 144, 0.6);
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

/* –¢–∞–π–º–µ—Ä */
.timer {
	color: #ffba66;
	font-weight: bold;
	font-size: 1.5rem;
	margin-bottom: 15px;
	text-shadow: 0 0 10px rgba(255, 186, 102, 0.8);
}

/* ID –∏–≥—Ä–æ–∫–∞ */
.player-id {
	position: absolute;
	bottom: 10px;
	right: 15px;
	font-size: 0.9rem;
	color: #ffffff;
	opacity: 0.4;
}

/* –ö–Ω–æ–ø–∫–∞ "—Å–∏–≥–Ω–∞–ª" */
.generate-btn {
	margin-top: 20px;
	padding: 10px 20px;
	background: #222;
	border: 2px solid #ffba66;
	color: #ffba66;
	font-size: 1rem;
	cursor: pointer;
	border-radius: 6px;
	transition: 0.3s;
}
.generate-btn:disabled {
	opacity: 0.5;
	cursor: not-allowed;
}
.generate-btn:hover:enabled {
	background: #333;
	box-shadow: 0 0 10px rgba(255, 186, 102, 0.6);
}

/* –¢–∞–π–ª—ã */
.tile {
	position: relative;
	width: var(--tile-size);
	height: var(--tile-size);
	background: rgba(255, 255, 255, 0.08);
	border-radius: 8px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	font-size: calc(var(--tile-size) / 2.5);
	color: #fff;
	overflow: hidden;
	transition: all 0.3s ease;
	box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.15);
	z-index: 1;
}
.tile::before {
	content: '';
	position: absolute;
	inset: 0;
	background-image: radial-gradient(
			1px 1px at 10px 15px,
			rgba(255, 255, 255, 0.8),
			transparent
		),
		radial-gradient(
			1px 1px at 25px 40px,
			rgba(255, 220, 180, 0.8),
			transparent
		),
		radial-gradient(
			1.2px 1.2px at 40px 20px,
			rgba(200, 200, 255, 0.9),
			transparent
		),
		radial-gradient(
			1px 1px at 60px 35px,
			rgba(255, 255, 255, 0.7),
			transparent
		),
		radial-gradient(
			1.5px 1.5px at 80px 60px,
			rgba(255, 240, 200, 0.9),
			transparent
		);
	background-repeat: repeat;
	background-size: 100px 100px;
	animation: twinkle 4s infinite alternate;
	opacity: 0.5;
	pointer-events: none;
}
@keyframes twinkle {
	0% {
		opacity: 0.2;
	}
	50% {
		opacity: 0.8;
	}
	100% {
		opacity: 0.4;
	}
}

/* –ü–æ–¥—Å–≤–µ—Ç–∫–∞ */
.tile:hover {
	transform: translateY(-3px);
	box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.3),
		0 6px 12px rgba(0, 0, 0, 0.6);
}

/* –ë–æ–º–±—ã */
.bomb-hint {
	background: rgba(255, 0, 0, 0.15);
	box-shadow: inset 0 0 12px rgba(255, 0, 0, 0.5),
		0 4px 8px rgba(0, 0, 0, 0.8);
	color: #ff4d4d;
	transition: all 0.3s ease;
}
.bomb-hint::before {
	content: '';
	position: absolute;
	inset: 0;
	background-image: radial-gradient(
			1px 1px at 15px 20px,
			rgba(255, 80, 80, 0.9),
			transparent
		),
		radial-gradient(
			1.2px 1.2px at 35px 50px,
			rgba(255, 0, 0, 0.8),
			transparent
		),
		radial-gradient(
			1px 1px at 55px 25px,
			rgba(255, 120, 120, 0.9),
			transparent
		),
		radial-gradient(
			1.3px 1.3px at 75px 60px,
			rgba(200, 0, 0, 0.9),
			transparent
		);
	background-repeat: repeat;
	background-size: 100px 100px;
	animation: twinkle 3s infinite alternate;
	opacity: 0.6;
	pointer-events: none;
}
.bomb-hint:hover {
	transform: translateY(-3px);
	background: rgba(255, 0, 0, 0.3);
	box-shadow: inset 0 0 20px rgba(255, 0, 0, 0.9),
		0 0 20px rgba(255, 0, 0, 0.8), 0 6px 12px rgba(0, 0, 0, 0.9);
	color: #ff6666;
}

/* üì± –ê–¥–∞–ø—Ç–∏–≤ */
@media (max-width: 768px) {
	.board {
		grid-template-columns: repeat(5, 45px);
		grid-template-rows: repeat(5, 45px);
		gap: 4px;
	}
	.tile {
		width: 45px;
		height: 45px;
		font-size: 1.2rem;
	}
	.timer {
		font-size: 1.2rem;
	}
	.generate-btn {
		padding: 8px 16px;
		font-size: 0.9rem;
	}
}

/* üì± –°–æ–≤—Å–µ–º –º–∞–ª–µ–Ω—å–∫–∏–µ (iPhone SE, Mini) */
@media (max-width: 400px) {
	.board {
		grid-template-columns: repeat(5, 35px);
		grid-template-rows: repeat(5, 35px);
		gap: 3px;
	}
	.tile {
		width: 35px;
		height: 35px;
		font-size: 1rem;
	}
	.timer {
		font-size: 1rem;
	}
	.generate-btn {
		padding: 6px 12px;
		font-size: 0.8rem;
	}
}

/*  –ê–Ω–¥—Ä–æ–∏–¥—ã */
